import {html} from "../../start";
import {customElement, ka_dom_ready, KaCustomElement, KaHtmlElement, template} from "@kasimirjs/embed";

html.push(require("./hc-order-qtty.html"))

// language=html
let tpl = `
<div style="display: inline-block">
    <div ka.if="$scope.quantity > 0" class="input-group w-auto">
        <button type="button"  ka.on.click="$scope.quantity--" class="btn btn-primary px-3">-</button>
        <input type="hidden" class="form-control" ka.attr.name="name" readonly size="3" ka.bind="$scope.quantity">
        <div class="input-group-text fw-bold px-3" id="btnGroupAddon">[[$scope.quantity]]</div>
        <button type="button" ka.on.click="$scope.quantity++" class="btn btn-primary px-3">+</button>
    </div>

    <button ka.if="$scope.quantity == 0" ka.on.click="$scope.quantity = 1" type="button" class="btn btn-primary">Bestellen</button>
</div>

`



@customElement("hc-order-qtty")
@template(tpl)
class HcOrderQtty extends KaCustomElement {
    html: ((element: KaHtmlElement) => Promise<HTMLTemplateElement | string>) | HTMLTemplateElement | string | null;

    constructor() {
        super();

        let scope = this.init({
            name: "undefined",
            quantity: 0
        })
    }

    async connectedCallback(): Promise<void> {
        super.connectedCallback();
        await ka_dom_ready();
        this.scope.name = this.getAttribute("name");
        this.scope.render();
    }
}
