import {HyperpageConfig} from "./types";

declare global {
    var HyperpageConfig : HyperpageConfig;
}

let defaults : HyperpageConfig = {
    cdnBaseUrl: "https://cdn.leuffen.de/hyperpage-components/v1.0/"
}

if (typeof window["HyperpageConfig"] !== "undefined") {
    defaults = {...defaults, ...window["HyperpageConfig"]}
}


import {html} from "./start";
import {HcSlider} from "./comp/slider/hc-slider";
export * from "./comp/accordion/accordion";
export * from "./comp/screens/screens";
export * from "./comp/google-maps/google-maps";
export * from "./comp/hero/hero-fullsize-overlay";
export * from "./comp/cards/hc-card";
export * from "./comp/image/hc-ratio-image";
export * from "./comp/alert/hc-alert";
export * from "./comp/dropdown/dropdown";
export * from "./comp/navbar/navbar";
export * from "./comp/slider/hc-slider";
export * from "./comp/sections/hc-sections";
export * from "./comp/forms/hc-forms";

//export * from "./autosec/hc-autosection";

export * from "./shop/order-qtty/hc-order-qtty";

let code = html.map((e : any) => e.default).join("").replace("@@cdnBaseUrl@@", defaults.cdnBaseUrl);

let elem = document.createElement("hyperpage-templates");
elem.style.display = "none";
elem.innerHTML = code;




// Add Templates as fast as possible
function addContent() {
     if(document.body)
         document.body.append(elem);
     else
         setTimeout(addContent, 1);
}
addContent();
